import * as React from 'react';
import styles from './ColumnSentimentAnalysis.module.scss';
import { IColumnSentimentAnalysisProps } from './IColumnSentimentAnalysisProps';
import { escape } from '@microsoft/sp-lodash-subset';

export default class ColumnSentimentAnalysis extends React.Component<IColumnSentimentAnalysisProps, {}> {
  public render(): React.ReactElement<IColumnSentimentAnalysisProps> {
    return (
      <div className={ styles.columnSentimentAnalysis }>
        <div className={ styles.container }>
          <div className={ styles.row }>
            <div className={ styles.column }>
              <span className={ styles.title }>Let's get started</span>
              <p className={ styles.subTitle }>Step by step with Column Sentiment Analysis</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
